<template>
  <div>
    <van-nav-bar
      title="习惯列表"
      left-text=""
      left-arrow
      @click-left="onClickLeft"
      class="shawdow"
    />
    <div class="logoWrapper">
      <img class="logo" src="../assets/2.gif" />
    </div>
    <div class="header1">
      习惯提醒列表:
    </div>
    <div class="container">
      <div class="divder" v-for="(item, index) in habits">
        <van-swipe-cell>
          <template slot="left">
            <van-button square type="primary" :text="'cron:' + item.cron" />
          </template>

          <van-cell :border="false" :title="item.content" />

          <template slot="right">
            <van-button
              square
              type="danger"
              @click="del(index, item.habit.id, item.jobKey, item.triggerKey)"
              text="删除"
            />
            <van-button
              square
              type="primary"
              @click="done(index, item.habit.id, item.jobKey, item.triggerKey)"
              text="完成"
            />
          </template>
        </van-swipe-cell>
      </div>
      <!--<div class="divder">-->
      <!--<van-swipe-cell>-->
      <!--<template slot="left">-->
      <!--<van-button square type="primary" text="选择" />-->
      <!--</template>-->

      <!--<van-cell :border="false" title="喝水" value="2019/8/12" />-->

      <!--<template slot="right">-->
      <!--<van-button square type="danger" text="删除" />-->
      <!--<van-button square type="primary" text="收藏" />-->
      <!--</template>-->
      <!--</van-swipe-cell>-->
      <!--</div>-->
    </div>
  </div>
</template>

<script>
import axios from "@/axios";
import mixin from "@/mixins/mixin.js";
export default {
  mixins: [mixin],
  name: "HabitList",
  data() {
    return {
      habits: []
    };
  },
  created() {
    this.updateDate();
  },
  methods: {
    updateDate() {
      axios.get("/api/get_habits").then(res => {
        if (res.data.code === 200) {
          this.habits = res.data.data;
        }
      });
    },
    del(index, id, jobKey, triggerKey) {
      axios({
        method: "delete",
        url: "/api/delete_habit",
        data: {
          id: id,
          jobKey: jobKey,
          triggerKey: triggerKey
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.habits.splice(index, 1);
          this.$toast("删除成功");
        }
      });
    },
    done(index, id, jobKey, triggerKey) {
      axios({
        method: "put",
        url: "/api/done_habit",
        data: {
          id: id,
          jobKey: jobKey,
          triggerKey: triggerKey
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.habits.splice(index, 1);
          this.$toast("更新成功");
        }
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
.container
    margin-top 5px
.divder
    border-bottom 1px solid #ebedf0
</style>
